import React from 'react';
import './App.css';
import axios from 'axios';
import GameCard from './components/gameCards';
import LoadingGif from './components/loading';

function App() {
  const [page, setPage] = React.useState(1)
  const [loading, setLoading] = React.useState(false);
  const [games, setGames] = React.useState([]);
  React.useEffect(() => {
    setLoading(true);
    axios.get(`https://frozen-ocean-18084.herokuapp.com/api?page=${page}`).then(response => {
      setGames(prevGames => [...prevGames, ...response.data.results]);
      setLoading(false);
    });
    // gameWrapperRef.current.addEventListener("scroll", function(){console.log('test')});
  }, [page]);

  const gamesWrapperHandleScroll = (e) => {
    const { scrollTop, clientHeight, scrollHeight } = e.currentTarget;
    if (scrollHeight - scrollTop === clientHeight) {
      setPage(page + 1);
    }
  }


  return (
    <div className="games-wrapper" onScroll={gamesWrapperHandleScroll}>
      {games.map(game => <div key={game.id} className="game-card-wrapper"><GameCard game={game} /></div>)}
      {loading ? <LoadingGif/> : null}
    </div>
  );
}

export default App;
